import React from 'react';

class TestError extends React.Component {
  constructor(props) {
    super(props);

    this.throwError = this.throwError.bind(this);
  }

  throwError() {
    // Example error, which will be reported to rollbar.
  }

  render() {
    var err = new Error('react child test error');
    err.foo = true;
    throw err;
    return (
      <React.Fragment>
        <h1>Rollbar Example for React Child Component</h1>
        <button id='child-error' onClick={ this.throwError }>Throw Child Error</button>
      </React.Fragment>
    );
  }
}

export default TestError;
